html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--red-hat-display);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--pale-blue);
  height: 100vh;
  position: relative;
}

:root {
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
}

:root {
  --red-hat-display: "Red Hat Display", sans-serif;
}

p {
  font-size: 1rem;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

.bg {
  height: 50vh;
}
@media (min-width: 23.4375em) {
  .bg__container img {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    height: 100%;
    width: 100%;
  }
}
@media (max-width: 23.375em) {
  .bg__container img {
    display: none;
  }
}

.card {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 15%;
  width: 100%;
  text-align: center;
}
@media (max-width: 23.375em) {
  .card {
    padding: 0 2rem;
  }
}
.card__container {
  background-color: var(--very-pale);
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30em;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0px 0.1px 9px var(--desaturated-blue);
}
.card__container .image {
  width: 100%;
}
.card__container .image img {
  display: block;
  width: 100%;
}
.card__container .text h1 {
  font-size: 1.875rem;
  font-weight: 900;
  margin-top: 2.5rem;
  color: var(--dark-blue);
}
.card__container .text p {
  display: inline-block;
  width: 20em;
  color: var(--desaturated-blue);
}
@media (max-width: 23.375em) {
  .card__container .text p {
    width: 16em;
  }
}
.card__container .text .plan {
  margin: 0.5rem 0 2rem 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.2rem;
  border-radius: 10px;
  background: rgba(224, 232, 255, 0.4);
}
@media (max-width: 23.375em) {
  .card__container .text .plan {
    width: 16em;
    margin: 0.5rem auto 2rem auto;
  }
}
.card__container .text .plan__first {
  display: flex;
  align-items: center;
}
.card__container .text .plan__first .info {
  margin-left: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.card__container .text .plan__first .info h3 {
  font-size: 1.04rem;
  font-weight: 900;
  color: var(--dark-blue);
}
@media (max-width: 23.375em) {
  .card__container .text .plan__first .info h3 {
    font-size: 0.9rem;
  }
}
.card__container .text .plan__first .info h4 {
  font-weight: 500;
  color: var(--desaturated-blue);
  font-size: 1.08rem;
}
@media (max-width: 23.375em) {
  .card__container .text .plan__first .info h4 {
    font-size: 0.9rem;
  }
}
.card__container .text .plan a {
  font-weight: 700;
  color: var(--bright-blue);
  transition: all 0.5s ease-in-out;
}
@media (max-width: 23.375em) {
  .card__container .text .plan a {
    font-size: 0.9rem;
  }
}
.card__container .text .plan a:hover {
  text-decoration: none;
  color: rgba(56, 41, 224, 0.8);
}
.card__container .text .btn {
  display: block;
  color: white;
  background: var(--bright-blue);
  border: none;
  padding: 1rem 7rem;
  border-radius: 8px;
  font-weight: 700;
  font-size: 0.9rem;
  margin-bottom: 1rem;
  cursor: pointer;
  box-shadow: 0px 2px 5px var(--bright-blue);
  transition: all 0.5s ease-in-out;
}
@media (max-width: 23.375em) {
  .card__container .text .btn {
    padding: 1rem 4rem;
  }
}
.card__container .text .btn:hover {
  background-color: rgba(56, 41, 224, 0.8);
}
.card__container .text .sec_btn {
  cursor: pointer;
  border: none;
  background: transparent;
  font-weight: 700;
  font-size: 0.9375rem;
  color: var(--desaturated-blue);
  margin: 0.8rem 0 3rem 0;
  transition: all 0.5s ease-in-out;
}
.card__container .text .sec_btn:hover {
  color: var(--dark-blue);
}